    margin: 0;
    overflow: hidden;
}

.dropdown_head.dropdown {
    display: inline-block;
    padding: 0 1em;
    text-align: left;
}
.product_head_dropdowns {
    display: inline-block;
    vertical-align: bottom;
}
.product_heading_left {
    display: inline-block;
}
.product_heading {
    padding: 1em 0.5em;
    min-height: 6em;
    margin-bottom: 1.5em;
}
.dropdown-toggle::after {
    border-top: none;
    border-left: none;
    border-right: 1px solid;
    border-bottom: 1px solid;
    width: 0.4em;
    height: 0.4em;
    transform: rotate(45deg);
}
.product_head_dropdowns .dropdown_head.dropdown:before {
    content: "";
    top: 0.2em;
    bottom: 0.2em;
    right: 0;
    width: 1px;
    background: #ddd;
    position: absolute;
}
.product_head_dropdowns .dropdown_head.dropdown:last-child:before {
    display: none;
}


.product_data_content:after {
    display: table;
    content: "";
    clear: both;
}
.product_menus_outer {
    width: 22em;
    height: calc(100vh - 7.5em);
    float: left;
/*   padding: 0 1em;*/
   overflow-x: hidden;
   overflow-y: auto;

}
.product_data_content_outer {
    width: calc(100% - 22em);
    float: left;
    height: calc(100vh - 7.5em);
    background: #fff;
    box-shadow: -4px -4px 18px rgba(20, 163, 245, 0.15);
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
}


/*accordion*/

ul.accordion ul, ul.accordion  {
    list-style: none;
    padding: 0;
}
.product_search_outer {
