  overflow:visible;
}


a.menu_add_btn {
    width: 1.35em;
    height: 1.35em;
    display: block;
    padding: 0.3em;
    border: 1px solid transparent;
    vertical-align: middle;
    border-radius: 2px;
    transition: all 0.2s ease;
}
a.menu_add_btn svg {
    width: 100%;
    height: 100%;
    display: block;
    fill: #888;
}
.dropdown_add {
    position: absolute;
    /* width: 1em; */
    /* height: 1em; */
