    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    animation-duration: 15s
}




/*@-webkit-keyframes sark-particle {
  0% {
    top: 10%;
    left:0
  }
  10% {
    top:0;
    left:15%;
    transform:rotate(-5deg)
  }
   25% {
    top:25%;
    left:50%;
    transform:rotate(5deg)
  }
   50% {
    top:10%;
    left:8%;
    transform:rotate(-5deg)
  }
 
}*/

@keyframes sark-particle {
  0% {
    top: 10%;
    left:0;
    opacity:1;
  }
  10% {
    top:0;
    left:15%;
    transform:rotate(-5deg);
  }
   25% {
    top:25%;
    left:50%;
    transform:rotate(5deg);
    opacity:0.6;
  }
   50% {
    top:10%;
    left:80%;
    transform:rotate(-5deg);
    opacity:0.4;
  }

  55% {
    top: 12%;
    left:90%;
    transform:rotateY(30deg);
    opacity:0.3;
  }
  60% {
    top: 16%;
    left:95%;
    transform:rotateY(60deg);
    opacity:0.25;
  }
  65% {
    top: 20%;
    left:92%;
    transform:rotateY(180deg);
    opacity:0.25;
  }
   75% {
    top: 50%;
    left:50%;
    transform:rotateY(180deg);
    opacity:0.15;
  }


 90% {
    top: 25%;
    left:15%;
    transform:rotateY(180deg);
    opacity:0.15;
  }

 95% {
    top: 25%;
    left:15%;
    transform:rotateY(60deg);
    opacity:0.25;
  }
  98% {
    top: 25%;
    left:10%;
    transform:rotateY(60deg);
    opacity:0.8;
  }

  100% {
   top: 10%;
    left:0;
    transform:rotateY(0deg);
    opacity:1;
  }
}












