.jf-table-border td, .jf-table-border th {
  border-right: 1px solid #e9eaec;
}
.jf-table-cell {
  padding-left: 18px;
  padding-right: 18px;
  text-overflow: ellipsis;
  white-space: normal;
  word-break: break-all;
  box-sizing: border-box;
  & .jf-form-item {
    margin-bottom: 0;
  }
}




























































































































































































































































































































